header {
    background-color: rgba(1, 35, 89, 0.563); /* Update header background color */
    color: #ffffff;
    padding: 20px;
    border-bottom: 1px solid #ddd;
    width: 100%;
  }
  
  header h1 {
    margin: 0;
    font-size: 3em; /* Increase font size for the main heading */
  }
  
  .main-content {
    max-width: 600px;
    margin: 20px;
    color: #ffffff;
  }
  
  .cta-button {
    background-color: rgba(4, 60, 150, 0.9);
    color: #ffffff;
    padding: 10px 20px;
    font-size: 1.2em; /* Increase font size for the button */
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease; /* Add smooth color transition on hover */
  }
  
  .cta-button:hover {
    background-color: #ffffff; /* Change button background color on hover */
    color: #4d90fe; /* Change button text color on hover */
  }
  
  footer {
    margin-top: auto;
    background-color: rgba(240, 240, 240, 0.9); /* Update footer background color */
    padding: 10px;
    width: 100%;
    text-align: center;
    color: #333333; /* Set footer text color to a dark shade */
  }
  